<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Jadwal Ujian</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-content class="ion-padding">
        <ion-list>
          <ion-item
            v-for="jadwal in jadwalUjian"
            :key="jadwal.id"
            @click="goToUjian(jadwal.id)"
          >
            <ion-label>
              <h2>{{ jadwal.mataPelajaran }}</h2>
              <p>{{ jadwal.kelas }}</p>
              <p>{{ formatDate(jadwal.tanggalUjian) }}</p>
            </ion-label>
          </ion-item>
        </ion-list>
      </ion-content>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonList,
  IonItem,
  IonLabel,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { getFirestore, collection, getDocs } from "firebase/firestore";
import { useRouter } from "vue-router";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const router = useRouter();
const jadwalUjian = ref([]);

const fetchJadwalUjian = async () => {
  const querySnapshot = await getDocs(collection(db, "jadwal"));
  jadwalUjian.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const goToUjian = (jadwalId) => {
  router.push({ name: "halamanujianUser", params: { jadwalId } });
};

const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  const date = new Date(timestamp.seconds * 1000);
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

onMounted(() => {
  fetchJadwalUjian();
});
</script>

